import './index.css'
import Cookies from 'js-cookie'
import {FiHome} from 'react-icons/fi'
import {CiShoppingCart, CiLogout} from 'react-icons/ci'
import {Link, useHistory, useLocation} from 'react-router-dom'

const Footer = () => {
  const history = useHistory()
  const location = useLocation()

  const logout = () => {
    Cookies.remove('jwt_token')
    history.replace('/login')
  }

  const isActive = (path) => location.pathname === path

  return (
    <div className="footer-container">
      <Link to="/">
        <img
          src="https://res.cloudinary.com/drji3ydue/image/upload/v1761320602/6fad20838855997d164dd88d885fad87bdfa3be6_or1vpx.png"
          alt="website logo"
          className="header-website-logo"
        />
      </Link>

      <div className="navigation-btns-container">

       
        <Link
          to="/"
          className={isActive("/") ? "active-footer-btn" : "footer-btns"}
        >
          <FiHome size={22} className="footer-icon" />
          <p className={isActive("/") ?"active-navigation-text":"navigation-text"}>Home</p>
        </Link>
        <Link
          to="/cart"
          className={isActive("/cart") ? "active-footer-btn" : "footer-btns"}
        >
          <CiShoppingCart size={22} className="footer-icon" />
          <p className={isActive("/cart") ?"active-navigation-text":"navigation-text"}>Cart</p>
        </Link>


        <button type="button" className="logout-btn" onClick={logout}>
          <CiLogout size={22} className="logout-icon" />
          <p className="logout-text">Logout</p>
        </button>

      </div>
    </div>
  )
}

export default Footer
